<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="box">
          <div class="order-loop">
            <div class="order-tit">
              <div class="left">{{ order.gysmc }}</div>
            </div>
            <div class="order-con">
              <div class="goods-img" @click="imgPreview(orderDetail.picture?orderDetail.picture:defaultImg)"><img :src="orderDetail.picture" :onerror="defaultImg"></div>
              <div class="goods-name">
                <div class="name">{{ orderDetail.product_name }}</div>
                <van-tag plain type="primary">{{ dsmc }}</van-tag>
              </div>
              <div class="goods-price">
                <div class="jiage">¥{{ orderDetail.price }}</div>
                <div class="shuliang">×{{ orderDetail.total }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title-sm">
            <div class="title-left">
              <h2>订单信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-left">订单编号</div>
            <div class="pagelist-right left">{{ order.ddbh }}</div>
          </div>
          <!-- <div class="page-list">
            <div class="pagelist-left">商城编号</div>
            <div class="pagelist-right left">{{ order.shop_order_id }}</div>
          </div> -->
          <div class="page-list">
            <div class="pagelist-left">下单时间</div>
            <div class="pagelist-right left">{{ getTime(order.add_time) }}</div>
          </div>
          <!-- <div class="page-list">
            <div class="pagelist-left">包装清单</div>
            <div class="pagelist-right left">装有 iOS 13 的 iPhone,采用闪电接头的 EarPods,闪电转 USB 连接线,USB 电源适配器,资料</div>
          </div> -->
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title-sm">
            <div class="title-left">
              <h2>服务类型</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-field
            v-model="servicesform.fwlxShow"
            v-validate="'required'"
            label="服务类型"
            required
            rows="3"
            :error-message="errors.first('fwlx')"
            autosize
            name="fwlx"
            readonly
            is-link
            placeholder="请选择服务类型"
            @click="showfwlxPopup"
          />
          <van-field
            v-model="servicesform.value"
            v-validate="'required'"
            label="提交数量"
            required
            rows="3"
            :error-message="errors.first('tjsl')"
            autosize
            name="tjsl"
            readonly
          >
            <template #input>
              <van-stepper v-model="servicesform.value" :max="orderDetail.total" />
            </template>
          </van-field>
          <van-field
            v-model="servicesform.sfbzShow"
            v-validate="'required'"
            label="是否有包装"
            required
            rows="3"
            :error-message="errors.first('sfybz')"
            autosize
            name="sfybz"
            readonly
            is-link
            placeholder="请选择是否有包装"
            @click="showbzPopup"
          />
          <van-field
            v-model="servicesform.desc"
            v-validate="'required'"
            label="问题描述"
            required
            rows="3"
            :error-message="errors.first('wtms')"
            autosize
            name="wtms"
            type="textarea"
            placeholder="请输入问题描述"
            maxlength="250"
          />
        </div>

        <div v-if="jklx===1 && dsbs!='KJXY'" class="common-main box">
          <div class="title-sm">
            <div class="title-left">
              <h2>确认信息</h2>
            </div>
          </div>
          <van-field v-model="servicesform.fhfs" v-validate="'required'" label="返件方式" required name="fjfs" :error-message="errors.first('fjfs')">
            <template #input>
              <van-radio-group v-model="servicesform.fhfs">
                <div v-for="(item,index) of fhfsMap" :key="index">
                  <van-radio :name="item.key">{{ item.value }}</van-radio>
                </div>
              </van-radio-group>
            </template>
          </van-field>

          <van-field
            v-model="servicesform.qhdzInput"
            v-validate="'required'"
            label="取货地址"
            required
            rows="3"
            :error-message="errors.first('shdz')"
            autosize
            name="shdz"
            is-link
            readonly
            placeholder="请选择取货地址"
            @click="showPopup('qhdzInput')"
          />
          <div class="page-list">
            <div class="pagelist-left">详细收货地址</div>
            <div class="pagelist-right">
              <van-field autosize row="2" placeholder="请输入具体地址" type="textarea" :value="servicesform.qhjtdz" />
            </div>
          </div>

          <van-field
            v-model="servicesform.fjdzinput"
            v-validate="'required'"
            label="返件地址"
            required
            rows="3"
            :error-message="errors.first('fjdz')"
            autosize
            name="fjdz"
            is-link
            placeholder="请选择返件地址"
            @click="showPopup('fjdzinput')"
          />
          <van-field
            v-model="servicesform.fjjtdz"
            v-validate="'required'"
            label="详细返件地址"
            required
            rows="3"
            :error-message="errors.first('xxdz')"
            autosize
            name="xxdz"
            type="textarea"
            placeholder="请输入详细地址"
          />
          <van-field
            v-model="servicesform.consignee"
            v-validate="'required'"
            label="客户姓名"
            required
            rows="3"
            :error-message="errors.first('khxm')"
            autosize
            name="khxm"
            placeholder="请输入客户姓名"
          />
          <van-field
            v-model="servicesform.mobile"
            v-validate="'required'"
            label="手机号码"
            required
            rows="3"
            :error-message="errors.first('sjhm')"
            autosize
            name="sjhm"
            placeholder="请输入手机号码"
          />
        </div>
        <div class="common-space" />
      </div>
      <!-- 底部按钮 begin -->
      <div class="foot-btn one fixed">
        <ul v-if="jklx===2 || dsbs==='KJXY'">
          <li><van-button type="info" round size="large" @click="ConfirmTj('save')">保存</van-button></li>
        </ul>
        <ul v-if="jklx===1 && dsbs!='KJXY'">
          <li><van-button type="info" round size="large" @click="ConfirmTj('submit')">提交</van-button></li>
        </ul>
      </div>
      <!-- 底部按钮 end -->

      <!-- 服务类型弹出层 beign -->
      <van-popup
        v-model="show1"
        position="bottom"
        round
      >
        <van-picker
          :columns="columns"
          show-toolbar
          @cancel="onCancel"
          @confirm="onConfirm"
        />
      </van-popup>
      <!-- 选择地址弹出层 end -->

      <!-- 包装弹出层 beign -->
      <van-popup
        v-model="show2"
        position="bottom"
        round
      >
        <van-picker
          :columns="columns2"
          show-toolbar
          @cancel="onCancel1"
          @confirm="onConfirm1"
        />
      </van-popup>
      <!-- 包装弹出层 end -->

      <!-- 返件方式弹出层 beign -->
      <van-popup
        v-model="show3"
        position="bottom"
        closeable
        round
      >
        <van-picker :columns="columns3" @change="onChange" />
      </van-popup>
      <!-- 返件方式弹出层 end -->

      <!-- 选择地址弹出层 beign -->
      <van-popup
        v-model="show"
        position="bottom"
        round
      >
        <XyAddress @getCheckResult="getCheckResult" />
      </van-popup>
    <!-- 选择地址弹出层 end -->

    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'
import XyAddress from '@/components/address/address.vue'
import { ImagePreview } from 'vant'
export default {
  name: 'OrderServiceApply',
  components: {
    headerBar,
    XyAddress
  },
  mixins: [pageRouter],
  data() {
    return {
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      dsmc: '',
      activeName: 'first',
      name: 'ServicesApply',
      orderDetailId: '',
      total: '',
      dsbs: '',
      beiz: '',
      jklx: '',
      order: [],
      orderDetail: [],
      fwlxMap: [],
      fhfsMap: [],
      servicesform: {
        value: '',
        fwlxShow: '',
        fwlx: '',
        num: 1,
        sfbzShow: '',
        sfbz: '',
        desc: '',
        fhfs: '',
        qhdz: [],
        fjdz: [],
        consignee: '',
        mobile: '',
        qhdzInput: '',
        qhjtdz: '',
        fjdzinput: '',
        fjjtdz: ''
      },
      checked: true,
      headerData: {
        title: '申请售后',
        leftUrl: 'back',
        leftText: ' '
      },
      address: '',
      show: false,
      show1: false,
      show2: false,
      show3: false,
      addressmodel: '',
      columns: [],
      columns2: ['是', '否'],
      addressStyle: '',
      columns3: ['上门取件', '客户发货', '客户送货'],
      town: '',
      county: '',
      city: '',
      province: '',
      town1: '',
      county1: '',
      city1: '',
      province1: ''
    }
  },
  created() {
    this.orderDetailId = this.$route.params.id
    this.getOrderDetail()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    onConfirm1(value, index) {
      if (value === '是') {
        this.servicesform.sfbzShow = '是'
        this.servicesform.sfbz = '1'
      } else {
        this.servicesform.sfbzShow = '否'
        this.servicesform.sfbz = '0'
      }
      this.show2 = false
    },
    onCancel1() {
      this.show2 = false
    },
    onConfirm(value, index) {
      for (var key in this.fwlxMap) {
        if (this.fwlxMap[key] === value) {
          this.servicesform.fwlx = key
          this.servicesform.fwlxShow = value
        }
      }
      this.show1 = false
    },
    onCancel() {
      this.show1 = false
    },
    getTime(item) {
      if (item) {
        item = item + ''
        return item.slice(0, 4) + '/' + item.slice(4, 6) + '/' + item.slice(6, 8) + ' ' + item.slice(8, 10) + ':' + item.slice(10, 12)
      } else {
        return ''
      }
    },
    getOrderDetail() {
      this.$store
        .dispatch('member/getAfsOrderDetailAction', { e_order_detail_id: this.orderDetailId })
        .then(res => {
          this.order = res.e_order
          this.orderDetail = res.e_order_detail
          for (var key in res.fwlxMap) {
            this.columns.push(res.fwlxMap[key])
          }
          this.fwlxMap = res.fwlxMap
          for (var key1 in res.fhfsMap) {
            this.fhfsMap.push({ 'key': key1, 'value': res.fhfsMap[key1] })
          }
          this.servicesform.consignee = res.e_order_distribution.consignee
          this.servicesform.mobile = res.e_order_distribution.mobile
          this.total = res.e_order_detail.total
          this.dsbs = res.e_order_detail.dsbs
          this.beiz = res.e_dsgl.beiz
          this.jklx = res.e_dsgl.jklx
          this.dsmc = res.e_dsgl.dsmc
          this.servicesform.province = res.e_order_distribution.province
          this.servicesform.city = res.e_order_distribution.city
          this.servicesform.county = res.e_order_distribution.county
          this.servicesform.town = res.e_order_distribution.town
          this.servicesform.placeholderqhdz = res.e_order_distribution.area
          this.servicesform.qhjtdz = res.e_order_distribution.address
          this.orderDetailId.push(this.orderDetailId)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (!state) {
        this.show = false
      }
      if (sheng) {
        this.address = sheng.text
      }
      if (shi) {
        this.address = this.address + '/' + shi.text
      }
      if (xian) {
        this.address = this.address + '/' + xian.text
      }
      if (xiang) {
        this.address = this.address + '/' + xiang.text
      }
      if (this.addressStyle === 'qhdzInput') {
        this.servicesform.qhdzInput = this.address
        if (sheng) {
          this.province = sheng.value
        }
        if (shi) {
          this.city = shi.value
        }
        if (xian) {
          this.county = xian.value
        }
        if (xiang) {
          this.town = xiang.value
        }
      } else {
        this.servicesform.fjdzinput = this.address
        if (sheng) {
          this.province1 = sheng.value
        }
        if (shi) {
          this.city1 = shi.value
        }
        if (xian) {
          this.county1 = xian.value
        }
        if (xiang) {
          this.town1 = xiang.value
        }
      }
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd'
      })
    },
    showPopup(addressStyle) {
      this.addressStyle = addressStyle
      this.show = true
    },
    showfwlxPopup() {
      this.show1 = true
    },
    showbzPopup() {
      this.show2 = true
    },
    showfjfsPopup() {
      this.show3 = true
    },
    onChange() {

    },
    // value=0改变省，1改变市，2改变区
    // onChange(picker, index, value) {
    //   const val = picker.getValues()
    //   console.log(val)// 查看打印
    //   let areaName = ''
    //   for (var i = 0; i < val.length; i++) {
    //     areaName = areaName + (i == 0 ? '' : '/') + val[i].name
    //   }
    //   this.addressmodel = areaName
    // },
    // 确定选择城市
    // onConfirm(val) {
    //   console.log(val[0].name + ',' + val[1].name)
    //   this.show = false// 关闭弹框
    // },
    // // 取消选中城市
    // onCancel() {
    //   this.show = false
    //   this.$refs.myArea.reset()// 重置城市列表
    // },
    ConfirmTj(flag) {
      this.$validator.validate().then(result => {
        if (result) {
          this.$dialog.confirm({
            title: '提示',
            message: '确认提交售后申请吗？'
          }).then(() => {
            let pre = null
            pre = {
              order_id: this.orderDetail.order_id,
              order_detail_id: this.orderDetail.id,
              customerexpect: this.servicesform.fwlx,
              num: this.servicesform.value,
              ishaspackage: this.servicesform.sfbz,
              questiondesc: this.servicesform.desc,
              returnwaretype: this.servicesform.fhfs,
              pickwaretype: this.servicesform.fhfs,
              pickwareprovince: this.province,
              pickwarecity: this.city,
              pickwarecounty: this.county,
              pickwarevillage: this.town,
              pickwareaddress: this.servicesform.qhjtdz,
              returnwareprovince: this.province1,
              returnwarecity: this.city1,
              returnwarecounty: this.county1,
              returnwarevillage: this.town1,
              returnwareaddress: this.servicesform.fjjtdz,
              customercontactname: this.servicesform.consignee,
              customermobilephone: this.servicesform.mobile
            }
            console.log('pre-----------------------------------------------------', pre)
            if (flag === 'save') {
              this.loading = true
              this.$store
                .dispatch('member/saveServicesAction', pre)
                .then(res => {
                  this.$toast('发送申请成功！')
                  this.$router.go(-1)
                })
                .catch(() => {
                  this.loading = false
                })
            } else if (flag === 'submit') {
              console.log(this.servicesform.fwlx)
              console.log(this.servicesform.fhfs)
              this.loading = true
              this.$store
                .dispatch('member/submitServicesAction', pre)
                .then(res => {
                  this.$toast('发送申请成功！')
                  this.$router.go(-1)
                })
                .catch(() => {
                  this.loading = false
                })
            }
          }).catch(() => {
            // on cancel
            this.$toast.fail('已取消')
          })
        }
      })
    }
  }
}
</script>

<style lang="css" scoped>
.spe-list .spe-left .info .van-radio-group{display: flex;}
.spe-list .spe-left .info .van-radio-group .van-radio{min-width: 60px;margin:5px 10px}
.pagelist-right{margin-left: 0}
</style>
